/* Tablet calendar adjustments */
@media (min-width: 641px) and (max-width: 1024px) {
  .fc .fc-toolbar-title {
    @apply text-base;
  }

  .fc .fc-button {
    @apply px-3 py-1.5 text-sm;
  }

  .fc .fc-daygrid-event {
    @apply text-sm;
  }
}

/* Always make calendar responsive and fit screen without vertical scroll */
.fc {
  @apply w-full overflow-x-auto;
}

.fc-view-harness {
  @apply overflow-x-auto;
  overflow-y: visible !important; /* No vertical scroll - all slots visible */
}

/* Ensure time grid view fits without scrolling */
.fc-timegrid {
  overflow-y: visible !important; /* Show all time slots without scrolling */
}

.fc-scroller {
  overflow-y: visible !important; /* Remove scroll on time grid */
  overflow-x: auto !important; /* Keep horizontal scroll for responsiveness */
}

/* Mobile-friendly modal styles - only for modals using these classes */
