  @apply w-full max-w-4xl mx-auto my-auto;
  @apply max-h-[95vh] overflow-y-auto;
  @apply p-4 sm:p-6 lg:p-8;
}

@media (max-width: 640px) {
  .modal-content:not(.custom-modal) {
    @apply rounded-lg max-h-[98vh] p-3;
  }

  .modal-overlay:not(.custom-modal) {
    @apply p-1;
  }
}

